* {
    font-family: "Google Sans";
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.about {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100vw;
    font-size: 60px;
    padding: 0 7%;
    
}

.Introduction {
    padding-top: 30%;
    font-weight: bold;
    margin-bottom: 3%;
}

.img {
    margin-top: 15vh;
    margin-left: 3vw;
    width: 57vh;
    height: 57vh;
    border-radius: 50%;
    filter: drop-shadow(0 0 0.75rem grey);
    max-width: 100%;
    max-height: 100%;
}

.Type {
    font-weight: bold;
    max-width: 45vw;
    max-height: 100%;
}

@media screen and (max-width: 1000px){
    .img{
        display: none;
    }

    .about{
        font-size:40px;
        font-weight:bold;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        text-align: center;
        height:fit-content;
    }

    .Type{
        font-size:28px;
        font-weight: bold;
        width:100vw;
    }

    .Check{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    


}