.contact{
    display:flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.title{
    text-align: center;
    font-size: 50px;
    font-weight: bold;
    padding-top: 10vh; 
    padding-bottom:10vh;  
}

.main{
    margin-top: 30vh;
    background-color: rgb(143, 143, 143);
    color:white;
    padding-bottom: 15vh;
}

@media screen and (max-width: 1000px){
    .contact{
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .main{
        margin-top: 20vh; 
    }
}